.login-container {
    width: 100%;
    height: 100vh;
    background-color: black;
    color: white;
    text-align: center;
    margin: 0;
}

.login-title-container {
    position: relative;
    top: 10%;
    background-color: black;
    margin: 0;

}
.login-title {
    font-family: 'Leckerli One', cursive;
    font-size: 7em;
}

.letter {

}

.letter:nth-child(1) {
    -webkit-animation: fadeIn 0.8s;
            animation: fadeIn 0.8s;
}
.letter:nth-child(2) {
    -webkit-animation: fadeIn 1.6s;
            animation: fadeIn 1.6s;
}
.letter:nth-child(3) {
    -webkit-animation: fadeIn 2.4s;
            animation: fadeIn 2.4s;
}
.letter:nth-child(4) {
    -webkit-animation: fadeIn 3.2s;
            animation: fadeIn 3.2s;
}
.letter:nth-child(5) {
    -webkit-animation: fadeIn 3.6s;
            animation: fadeIn 3.6s;
}
.letter:nth-child(6) {
    -webkit-animation: fadeIn 4.0s;
            animation: fadeIn 4.0s;
}
.letter:nth-child(7) {
    -webkit-animation: fadeIn 4.6s;
            animation: fadeIn 4.6s;
}
.letter:nth-child(8) {
    -webkit-animation: fadeIn 5.0s;
            animation: fadeIn 5.0s;
}
.letter:nth-child(9) {
    -webkit-animation: fadeIn 5.4s;
            animation: fadeIn 5.4s;
}
.letter:nth-child(10) {
    -webkit-animation: fadeIn 6.0s;
            animation: fadeIn 6.0s;
}

.login-subtitle{
    position: relative;
    font-family: 'Leckerli One', cursive;
    left: 9%;
    font-size: 3em;
    margin: 0;
    -webkit-animation: fadeIn 4s !important;
            animation: fadeIn 4s !important;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


.slack-btn-container {
    position: relative;
    top: 20%;
    margin:0 auto;
    -webkit-animation: fadeIn 3s !important;
            animation: fadeIn 3s !important;
}

.slack-btn-container :hover {
    cursor: pointer;
}

.guest-btn-container {
    border-radius: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 22%;
    width: 344px;
    background-color: white;
    margin:0 auto;
    -webkit-animation: fadeIn 3s !important;
            animation: fadeIn 3s !important;
    text-align: left;
    height: 80px;
    padding: 10px;
    color: black;
    border: solid lightgrey 2px;
}

.guest-btn-container :hover {
    cursor: pointer;
}

#guest-login-img {
    width: 55px;
    display: inline-block;
}

#guest-login-text {
    font-size: 40px;
    font-weight: bold;
    width: 100%;
    height: 100%;
    padding-left: 20px;
}
.place-search-list {
    background-color: black;
    color : white;
    padding: 1em 0 1em 2em;
    border: 0.2em solid white;
    border-radius: 0.5em;
    width: 100%;
    height: auto;
}
#nav_bottom_line {
    background-color: grey;
    width: 100%;
    height: 5px;
    position: absolute;
    bottom: 0;
}

.back-black {
    background-color: black;
}

.logo-font {
    font-family: Leckerli One, cursive;
}

#filter-ul {
    margin-left: 50px;
    font-weight: bold;
}

.post-icon-container {
    height: 100%;
}

.post-icon-container:hover {
    cursor: pointer;
}

#post-restaurant-btn {
    margin-right: 50px;
    color: white;
    height: 100%;
    font-weight: bold;
    padding: 0 12px;
}

#nav-post-icon {
    display: inline-block;
}
.modal-wrapper {
    position: fixed;
    z-index: 50;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
}

/* Modal Content/Box */
.modal-content {
    border-radius: 30px;
    background-color: #fefefe;
    margin: 15% auto; /* 15% from the top and centered */
    padding: 20px;
    border: 1px solid #888;
    width: 500px;
}



.modal-title {
    display: inline-block;
    width: 70%;
    color: black;
    font-size: 22px;
    font-weight: bold;
}

Input {
    color: black;
}

.post_radio {
    margin-right: 30px;
    color: black;
}

#post-input:focus {
    border-bottom: 1px solid #000000 !important;
    box-shadow: 0 1px 0 0 #000000 !important;
}

#post-textarea {
    width:100%;
    height:15%;
}
#post-textarea:focus {
    outline: none !important;
    border-bottom: 1px solid #000000;
    box-shadow: 1px 1px #000000;
}

#radio-container {}

#post-header {
    text-align: center;
    color: black;
}

.post-btn {
    height: 50px;
    width: 50%;
    border-radius: 5px;
    background-color: black;
    color: white;
}

.post-btn:hover {
    background-color: #888888;
}
.dotOverlay {
    position:relative;
    bottom:10px;
    border-radius:6px;
    border: 1px solid #ccc;
    border-bottom:2px solid #ddd;
    float:left;font-size:12px;
    padding:5px;
    background:#fff;
}
.dotOverlay:nth-of-type(n) {border:0; box-shadow:0px 1px 2px #888;}
.number {
    font-weight:bold;
    color:#ee6152;}
.listitem_container {
    margin: 0;
    height: 100px;
    cursor: pointer;
}

.active_list {
    border: 5px solid #151719;
}

.listitem_img {
    width: 100%;
    height: 100%;
    background-color: white;
}

.listitem_card_container {
    width: 120px;
}

.card_header {
    text-align: center;
    margin-top: 0;
    font-weight: bold;
}

.card_foot {
    text-align: center;
}

.listitem_ratingpoint {
    margin-right: 10px;
}

.con {
    z-index: 3;
}

.first {
    background-color: gold;
}

.second {
    background-color: #e0e0e0;
}

.defaultBack {
    background-color: #fae7df;
}

.hell {
    background-color: white;
}

#list_container {
    background-color: white;
}

#list_container ul {
    margin: 0;
    overflow: scroll;
    height: 100vh;
}

#list_top {
    width: 100%;
    height:10em;
    background-color: black;
    padding-top: 10px;
}

#list_top p {
    color: white;
    margin-top: 0;
    margin-bottom: 20px;
    text-align: center;
}

#list_top h4 {
    color: white;
    margin: 5px;
    text-align: center;
}
#review_star {
  color: gold;
}

#review_content {
  max-width: 100%;
  margin-top: 10px;
  white-space: pre-line;
}

#review_title {
  color: darkGrey;
}
#reviews_container {
    position: fixed;
    margin: 0;
    top: 30%;
    left: -20%;
    width: 20%;
    max-height: 68%;
    z-index: 5;
    border: solid 3px #151719;
    overflow: scroll;
}
#reviews_container.active_reviews {
    left: 1%;
    -webkit-transition: all 500ms linear;
    transition: all 500ms linear;
}
#review-list-top {
    height: auto;
}
#create_review_btn {
    font-size: 20px;
    float: right;
    cursor: pointer;
}

.modal_btn {
    background-color : black;
    margin : 0.5em;
}

.review-post-btn {
    width: 100%;
    background-color: black;
}

#review-list-title {
    font-size: 20px;
    display: inline-block;
    width: 73%;
}

#review_list_bot {
    text-align: center;
    width: 100%;
    background-color: white;
    color: gray;
    padding: 10px;
}

#review_list_bot :hover {
    cursor: pointer;
}
.write-btn {
    height: 50px;
    width: 50%;
    border-radius: 5px;
    background-color: black;
    color: white;
}

.review-post-btn:hover,
.review-post-btn:focus {
    background-color: #888888;
}

.write-textarea {
    width:100%;
    height:55%;
}

.write-header {
    border-bottom: lightgrey solid 1px;
    margin-bottom: 30px;
    padding: 10px 5px;

}

.write-btn:hover {
    background-color: #888888;
}

textarea {resize: none}
#detailed {
    position: fixed;
    margin: 0;
    top: 30%;
    right: -30%;
    background-color: black;
    z-index: 3;
}

#detailed h4 {
    color: white;
    text-align: center;
    margin: 10px;
}

#detailed p {
    color: white;
    text-align: center;
    letter-spacing: 2.5px;
    margin: 20px;
}

#detailed.active_info {
    right: 26%;
    -webkit-transition: all 400ms linear;
    transition: all 400ms linear;
}

#loading {
    color: black;
    z-index: 100;
    position:absolute;
    top: 10%;
    left:29%;
    font-size: 10em;
    font-family: Do Hyeon, sans-serif;
}
